import React from "react";
import { getUserFromCookieServer } from "../../lib/auth";

export default async function TopRightUser() {
  const user = await getUserFromCookieServer();
  if (!user) return null;

  return (
    <div className="top-right-user">
      <span className="badge bg-light text-dark">@{user.username}</span>
    </div>
  );
}
